	module.exports = {"data":{"markdownRemark":{"html":"<p>到楼后面的小花园，是一个古典式小花园，但是好像长时间没有人去，已经有一点荒芜了。在那里，有几块大的石头堆的假山上，上面有乾隆的诗。一首写的是：</p>\n<p>阅耕亭子青云表，锄雨犁烟悯家勤。每值秋来云稍慰，绿运即渐作黄云。第二首是：绿甸高低绘麦禾，犁云锄雨较如何。一年家是开心处，夏春兹多乐不多。好像写得也是一般，意境尚可。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0085.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0085.md","frontmatter":{"title":"5月3日 星期二"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0085.md absPath of file >>> MarkdownRemark"}}

